@import './../../../assets/styles/_styles.scss';

.header {
    top: 0;
    height: 95px;
    width: 100%;
    display: flex;
    align-items: center;
    z-index: 999;
    position: fixed;
    @extend %main-transition;
    @media (max-width: 1024px) {
        height: 65px;
    }
    &_fixed {
        background: #fff;
        @extend %main-transition;
    }
}
.container {
    @extend %container
}
.content {
    display: flex;
    align-items: center;
    gap: 0 40px;

    @media (min-width: 1025px) {
        justify-content: space-between;
    }

    @media (max-width: 550px) {
        gap: 0 20px;
    }
}

.logo {
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #000000;
    display: flex;
    align-items: center;
    gap: 0 15px;

    @media (max-width: 1024px) {
        margin-right: auto;
    }
}
.nav {
    @extend %main-transition;

    @media (max-width: 1024px) {
        position: fixed;
        top: 0;
        right: -320px;
        width: 320px;
        height: 100%;
        z-index: 9999;

    }

    @media (max-width: 425px) {
        width: 100%;
        right: -100%;
    }

    &_active {
        right: 0;
        @extend %main-transition;
    }

    &_body {
        display: flex;
        gap: 10px 45px;

        @media (max-width: 1024px) {
            flex-direction: column;
            background: #6E9C9F;
            height: 100%;
            padding: 24px 70px 24px 30px;
        }
    }

    &_link {
        font-weight: 400;
        font-size: 15px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: #000000;
        @extend %main-transition;

        &_active {
            color: #6E9C9F;
            font-weight: 700;
        }

        &:hover {
            @extend %main-transition;
            color: #6E9C9F;
        }

        @media (max-width: 1024px) {
            color: #fff;
            width: 100%;
            display: inline-block;

            &.active {
                color: #FFF;
            }
        }
    }
}
.contact {
    display: flex;
    align-items: center;
    gap: 0 15px;
    background: transparent;
    &:hover {
        & .header__contact-icon {
            @extend %main-transition;
            background: #6E9C9F;

            & svg path {
                @extend %main-transition;
                fill: #fff;
            }
        }
    }

    &_icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 27px;
        height: 27px;
        @extend %main-transition;

        @media (max-width: 470px) {
            width: 20px;
            height: 20px;
        }

        & svg {
            @media (max-width: 470px) {
                width: 20px;
                height: 20px;
            }

            & path {
                @extend %main-transition;
            }
        }
    }

    &_text {
        font-weight: 400;
        font-size: 15px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: #000000;

        @media (max-width: 470px) {
            display: none;
        }
    }
}
.cart {
    position: relative;

    &_items {
        top: -8px;
        right: -12px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        font-weight: 400;
        font-size: 9px;
        line-height: 100%;
        text-align: center;
        letter-spacing: 0.02em;
        color: #FFFFFF;
        width: 15px;
        height: 15px;
        background: #998E78;
        border-radius: 50px;
    }
}
.burger {
    background: transparent;
    width: 100%;
    max-width: 30px;
    height: 18px;
    position: relative;
    z-index: 99999;

    @media (min-width: 1025px) {
        display: none;
    }

    & span {
        position: absolute;
        left: 0;
        background: #000;
        width: 100%;
        height: 2px;
        @extend %main-transition;

        &:nth-child(1) {
            top: 0;
        }

        &:nth-child(2) {
            top: 8px;
        }

        &:nth-child(3) {
            top: 8px;
        }

        &:nth-child(4) {
            bottom: 0;
        }
    }

    &_active {
        & span {
            @extend %main-transition;
            background: #fff;

            &:nth-child(1) {
                opacity: 0;
                left: 100px;
            }

            &:nth-child(2) {
                top: 8px;
                transform: rotate(45deg);
            }

            &:nth-child(3) {
                top: 8px;
                transform: rotate(-45deg);
            }

            &:nth-child(4) {
                left: 100px;
                opacity: 0;
            }
        }
    }
}