@import './../../../assets/styles/styles';

.wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    width: fit-content;
    margin: 60px auto 0;
    flex-wrap: wrap;
    position: relative;
}

.prev,
.next {
    background: transparent;
    bottom: -30px;
    @media (max-width: 375px) {
        position: absolute;
    }
    &__disabled {
        opacity: 0.4;
        pointer-events: none;
    }
}
.prev {
    right: 60%;
}
.next {
    left: 60%;
}

.button {
    background: transparent;
    width: 41px;
    height: 41px;
    border: 1px solid #000;
    @extend %default-text;

    &__active {
        background: #000;
        color: #fff;
    }
}