@import '_styles.scss';

.container {
    @extend %container;
}

.about {
    &__block {
        display: flex;
        align-items: center;
        gap: 20px 95px;
        justify-content: space-between;
        margin-bottom: 130px;

        &:nth-child(even) {
            flex-direction: row-reverse;
        }

        @media (max-width: 1024px) {
            gap: 20px 40px;
            margin-bottom: 60px;
        }

        @media (max-width: 768px) {
            flex-direction: column !important;
        }

        &_img {
            max-width: 440px;
            width: 100%;
        }

        &_pic {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }

        &_text {
            max-width: 540px;
            width: 100%;

            @media (max-width: 768px) {
                max-width: 440px;
            }
        }

        &_title {
            @extend %title-H3;
            padding-bottom: 46px;

            @media (max-width: 768px) {
                padding-bottom: 20px;
            }
        }

        &_info {
            @extend %default-text;

            &:not(:last-child) {
                padding-bottom: 23px;

                @media (max-width: 768px) {
                    padding-bottom: 16px;
                }
            }
        }
    }
}